import React from 'react'
import { useNavigate } from 'react-router-dom'

export default function TestAuction() {
    const navigator = useNavigate();

    const btnClicked = (data) => {
        navigator(data)
    }

    return (
        <div className="home">
            <h1 className="title">롤 팀 모의 경매장</h1>
            <div className="button-box">
 
                <button className="btn btn-border" onClick={() => {btnClicked('/test-register')}}>
                    선수 등록
                </button>

                <button className="btn btn-border" onClick={() => {btnClicked('/test-join')}}>
                    경매 참가
                </button>
                

            </div>
        </div>
    )
}
