import React from 'react'
import { useState } from 'react'
import Modal from '../component/Modal'
import Select from 'react-select'

export default function TestKickModal({data, isOpen, closeModal,updatePlayer }) {
    const [kickPlayer, setKickPlayer] = useState([])
    
    const playerList = Object.keys(data) && Object.keys(data).map((player,index) => {
        console.log(data)
        return {
            value: data[player].twitchName,
            label: data[player].twitchName,
        }
    })
    console.log(playerList)

    const kickModalStyle = {
        container: (provided) => ({
            ...provided,
            maxWidth: '350px',
            width: '100%',
        }),
        control: (provided) => ({
            ...provided,
            padding: '0.5em 0em'
        }),
        placeholder: (provided) => ({
            ...provided,
            fontSize: '15px',
            fontFamily: 'LeferiPoint-WhiteA',
            textAlign: 'left',
        })
    }
    const updateKickPlayer = (data) => {
        setKickPlayer(data)
    }
    const postKickPlayer = (e) => {
        closeModal(e)
        updatePlayer(kickPlayer)

    }
    
    return (
        <Modal isOpen={isOpen} closeModal={closeModal}>
            <h2>내보낼 사람을 선택해주세요</h2>
            <Select
                styles={kickModalStyle}
                options={playerList}
                isMulti={true}
                onChange={updateKickPlayer}
                placeholder='내보낼 사람을 선택해주세요!'
            />
            <button type="button" className="codeBtn" onClick={(e) => { postKickPlayer(e) }}>완료</button>
        </Modal>
    )
}
