import logo from './logo.svg';
import './App.css';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Home from './screens/Home';
import CreateAuction from './screens/CreateAuction';
import JoinAuction from './screens/JoinAuction';
import RegisterAuction from './screens/RegisterAuction';
import Login from './screens/Login';
import { RecoilRoot } from 'recoil';
import LoginRoute from './screens/LoginRoute';
import TestAuction from './screens/testScreens/TestAuction';
import TestRegister from './screens/testScreens/TestRegister';
import TestJoin from './screens/testScreens/TestJoin';

function App() {
    return (
		<RecoilRoot>
			<BrowserRouter basename={process.env.PUBLIC_URL}>
				<Routes>
					<Route element={<LoginRoute/>}>
						<Route path="/" element={<Home/>} />
						<Route path="/login" element={<Login/>} />
						<Route path="/create-auction" element={<CreateAuction/>} />
						<Route path="/register-auction" element={<RegisterAuction/>} />
						<Route path="/join-auction" element={<JoinAuction/>} />
					</Route>
					<Route path="/test-auction" element={<TestAuction/>} />
					<Route path="/test-register" element={<TestRegister/>} />
					<Route path="/test-join" element={<TestJoin/>} />
				</Routes>
			</BrowserRouter>
		</RecoilRoot>
    );
}

export default App;
